<template>
  <div id="enterpriseHome">
    <div id="left">
      <div id="location">
        当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > <span class=red>企业</span>
      </div>
      <div id="enterpriseNews">
        <i class="fa fa-align-justify" aria-hidden="true"></i>
        <div class="more">更多</div>
        <div id="enterpriseNewsPics" @click="nextPic()">
          <div id="picsSelect">
            <button v-for="(i,index) in newsPics" class="selectButton" @click.stop="nextPic(index)">{{index + 1}}</button>
          </div>
          <transition name="fade" enter-active-class="slideInRight" leave-active-class="slideOutLeft">
            <div v-if="play==0" class="newsPicFrame">
              <img class="newsPic" v-bind:src="newsPics[play1]">
              <br>
              <div class="newsText">这是一段新闻介绍，北京申奥成功啦！这是一段新闻介绍，北京申奥成功啦!这是一段新闻介绍，北京申奥成功啦!这是一段新闻介绍，北京申奥成功啦!</div>
            </div>
          </transition>
          <transition name="fade" enter-active-class="slideInRight" leave-active-class="slideOutLeft">
            <div v-if="play==1" class="newsPicFrame">
              <img class="newsPic" v-bind:src="newsPics[play2]">
              <br>
              <div class="newsText">这是一段新闻介绍，北京申奥成功啦！这是一段新闻介绍，北京申奥成功啦!这是一段新闻介绍，北京申奥成功啦!这是一段新闻介绍，北京申奥成功啦!</div>
            </div>
          </transition>
        </div>
      </div>
      <div id="enterpriseDisplay">
        <div @click="routerLink('/enterprise/enterpriseHall')" class="more">更多</div>
        <div class="smallTitle">
          <i class="fa fa-picture-o" aria-hidden="true"></i> 品牌展厅
        </div>
        <br>
        <div class="flexbox">
          <img v-for="i in enterpriseDisplay" v-bind:src="i" class="displayPic" @click="routerLink('/enterprise/enterpriseDisplay')">
        </div>
      </div>
      <div id="memberState">
        <i class="fa fa-users" aria-hidden="true"></i>
      </div>
      <br>
    </div>
    <enterpriseRight></enterpriseRight>
  </div>
</template>

<script>
import axios from "axios";
import enterpriseRight from "@/components/enterprise/enterpriseRight";
export default {
  name: "enterpriseHome",
  data() {
    return {
      newsPics: [
        "./static/PICS/timg.jpg",
        "./static/PICS/timg2.jpg",
        "./static/PICS/timg3.jpg",
        "./static/PICS/timg4.jpg",
        "./static/PICS/timg5.jpg"
      ],
      enterpriseHot: [
        "新松机器人科技天团亮相世界机器人大会",
        "2018年上边年广东工业机器人产量占全国1/5",
        "机器视觉公司扩博智能完成1100万美元融资",
        "2018世界机器人大会在北京举办"
      ],
      enterpriseDisplay: [
        "./static/PICS/company1.png",
        "./static/PICS/company2.png",
        "./static/PICS/company3.png",
        "./static/PICS/company1.png",
        "./static/PICS/company2.png",
        "./static/PICS/company3.png"
      ],
      getBrand: undefined,
      getCompanyNews: undefined,
      play: 0,
      play1: 0,
      play2: 1
    };
  },
  components: {
    enterpriseRight
  },
  methods: {
    routerLink: function(path, params) {
      this.$router.push(path);
    },
    nextPic: function(num) {
      //num表示要播放的，如果没有就表示下一张
      if (this.play == 0) {
        if (num == this.play1) return false;
        this.play2 =
          num !== undefined ? num : (this.play1 + 1) % this.newsPics.length;
      } else {
        if (num == this.play2) return false;
        this.play1 =
          num !== undefined ? num : (this.play2 + 1) % this.newsPics.length;
      }
      this.play = (this.play + 1) % 2;
    }
  },
  created: function() {
    this.axios.get(this.HOST + "/company/getBrand").then(res => {
      this.getBrand = res;
      console.log(this.getBrand);
    });
    this.axios.get(this.HOST + "/company/getCompanyNews").then(res => {
      this.getCompanyNews = res.message;
      console.log(res.result);
    });
  }
};
</script>
<style lang="scss" scoped>
#enterpriseHome,
#enterpriseHome /deep/ #enterpriseRight {
  /*              */
  /*   ↖光↑↑翼↗   */
  /*   ←( ·A·)→   */
  /*   ↙展↓↓开↘   */
  /*              */
  div.flexbox {
    width: 100%;
    display: flex;
    display: -webkit-flex; /* Safari */
    justify-content: center;
    flex-wrap: wrap;
  }
  .flexbox2 {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
  }
  .chosenRed {
    color: #127de7;
    position: relative;
    padding-bottom: 5px;
    border-bottom: 2px solid #127de7;
  }
  .cursordefault {
    cursor: default;
  }
  .cursordefault:after {
    content: "";
    height: 100%;
  }
  .red {
    color: #127de7;
  }
  .gray {
    color: gray;
  }
  .gray:hover {
    color: #127de7;
  }
  .selectable:hover {
    color: #127de7;
  }
  .more {
    color: #127de7;
    float: right;
    display: inline-block;
  }
  .more:hover {
    cursor: pointer;
  }
  div#left {
    float: left;
    position: relative;
    top: 10px;
    width: 710px;
  }
  div#right {
    float: right;
    position: relative;
    top: 10px;
    width: 480px;
  }
  div#enterpriseHot,
  div#enterpriseMine,
  div#enterpriseOthers,
  div#advertisePlace {
    width: auto;
    padding: 5px;
    border: 2px solid rgb(222, 222, 222);
    margin-bottom: 10px;
  }
  div#enterpriseHot:after {
    content: "企业热点";
    position: absolute;
    top: 5px;
    left: 1.5rem;
  }
  div#enterpriseHot:before {
    content: "";
    width: 100%;
    position: absolute;
    top: 2rem;
    left: 0;
    border-top: 2px solid rgba(222, 222, 222, 1);
  }
  div#enterpriseHot > div.more {
    margin-bottom: 10px;
  }
  div#hotTitle {
    position: relative;
    top: 1.9rem;
    margin-bottom: 2.9rem;
  }
  div#userCenter,
  div#takeNews,
  div#myDeal,
  div#recentActive,
  div#communityActive,
  div#other {
    min-height: 200px;
    margin-top: 5px;
  }
  div#advertisePlace {
    min-height: 300px;
  }
  button {
    outline: none;
  }
  div#location,
  div#enterpriseDisplay,
  div#memberState {
    padding: 5px;
    width: 700px;
    border: 2px solid rgba(222, 222, 222, 1);
    margin-bottom: 10px;
    position: relative;
    background: rgba(255, 255, 255, 0.95);
  }
  div#enterpriseNews {
    display: inline-block;
    padding: 5px;
    width: 700px;
    height: 420px;
    border: 2px solid rgba(222, 222, 222, 1);
    position: relative;
    overflow: hidden;
    margin-bottom: 5px;
    background: rgba(255, 255, 255, 0.95);
  }
  div#enterpriseNews:after {
    content: "企业新闻";
    position: absolute;
    left: 1.7rem;
    top: 5px;
    height: 23px;
    line-height: 23px;
  }
  div#memberState:after {
    content: "会员动态";
    position: absolute;
    left: 1.7rem;
    height: 23px;
    line-height: 23px;
  }
  div#memberState {
    min-height: 300px;
  }
  div#enterpriseNewsPics {
    width: 700px;
    height: 395px;
    margin-top: 5px;
    overflow: hidden;
    white-space: nowrap;
    position: absolute;
  }
  div#picsSelect {
    position: absolute;
    bottom: 35px;
    left: 50%;
    z-index: 999;
    transform: translateX(-50%);
  }
  button.selectButton {
    background: white;
    color: black;
    margin: 0px 8px;
    border: 0;
    width: 1.2rem;
    height: 1.2rem;
    font-size: 0.8rem;
    border-radius: 5px;
  }
  div.newsPicFrame {
    margin: 0;
    width: 700px;
    height: 395px;
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
  }
  img.newsPic {
    vertical-align: bottom;
    width: 700px;
    height: 395px;
  }
  .newsText {
    color: white;
    position: absolute;
    font-size: 1rem;
    height: 1.8rem;
    line-height: 1.8rem;
    bottom: 0px;
    left: 0px;
    width: 695px;
    padding-left: 5px;
    background: rgba(0, 0, 0, 0.3);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  img.displayPic {
    width: 100px;
    height: 100px;
    margin: 15px;
  }
  button:hover {
    cursor: pointer;
  }
  div#enterprise {
    width: 1200px;
    margin: 0 auto;
  }
  img.displayPic {
    width: 100px;
    height: 100px;
    margin: 15px;
  }
  .more {
    color: #127de7;
    float: right;
    display: inline-block;
  }
  .more:hover {
    cursor: pointer;
  }
  div#location {
    padding: 5px;
    width: 700px;
    border: 2px solid rgba(222, 222, 222, 1);
    margin-bottom: 10px;
    background: rgba(255, 255, 255, 0.95);
  }
  div#location,
  div.bigTitle {
    padding: 5px;
    width: 700px;
    border: 2px solid rgba(222, 222, 222, 1);
    margin-bottom: 10px;
  }
  div.bigTitle {
    padding: 15px 5px;
    text-align: center;
    font-size: 1.8rem;
    font-weight: bold;
    letter-spacing: 6px;
    margin: 0;
  }
  div.famousFrame,
  div.membersFrame {
    text-align: center;
    margin-bottom: 10px;
  }
  img.famousPic {
    height: 150px;
    width: 150px;
    margin: 20px;
  }
  img.membersPic {
    height: 120px;
    width: 120px;
    margin: 15px;
  }
  div#enterpriseHot,
  div#enterpriseMine,
  div#enterpriseOthers,
  div#advertisePlace {
    width: auto;
    padding: 5px;
    border: 2px solid rgb(222, 222, 222);
    margin-bottom: 10px;
    background: rgba(255, 255, 255, 0.95);
  }
  div#enterpriseHot:after {
    content: "企业热点";
    position: absolute;
    top: 5px;
    left: 1.5rem;
  }
  div#enterpriseHot:before {
    content: "";
    width: 100%;
    position: absolute;
    top: 2rem;
    left: 0;
    border-top: 2px solid rgba(222, 222, 222, 1);
  }
  div#enterpriseHot > div.more {
    margin-bottom: 10px;
  }
  div#hotTitle {
    position: relative;
    top: 1.9rem;
    margin-bottom: 2.9rem;
  }
  div#userCenter,
  div#takeNews,
  div#myDeal,
  div#recentActive,
  div#communityActive,
  div#other {
    min-height: 200px;
    margin-top: 5px;
  }
  div#advertisePlace {
    min-height: 300px;
  }
}
</style>



