<template>
    <div id="enterpriseDisplay">
        <div id="left">
            <div id="location">
                当前位置：<router-link to="/home" class="selectable">服务平台</router-link> > 
                <router-link to="/enterprise" class="selectable">企业</span></router-link> >
                <span class=red>xxx有限公司</span>
            </div>
        </div>
        <enterpriseRight></enterpriseRight>
    </div>
</template>

<script>
import enterpriseRight from '@/components/enterprise/enterpriseRight';
export default {
    name: 'enterpriseDisplay',
    data(){
        return {
            enterpriseDisplay:[
                "./static/PICS/company1.png",
                "./static/PICS/company2.png",
                "./static/PICS/company3.png",
                "./static/PICS/company1.png",
                "./static/PICS/company2.png",
                "./static/PICS/company3.png"
            ],
        }
    },
    components:{
        enterpriseRight
    },
    methods:{
        routerLink:function(path){
            this.$router.push(path)
        }
    }
}
</script>
<style lang="scss" scoped>
#enterpriseDisplay,#enterpriseDisplay /deep/ #enterpriseRight{  
/*              */
/*   ↖光↑↑翼↗   */
/*   ←( ·A·)→   */
/*   ↙展↓↓开↘   */
/*              */
    div.flexbox{
      width:100%;
      display:flex;
      display: -webkit-flex; /* Safari */
      justify-content:center;
      flex-wrap:wrap;
    }
    .flexbox2{
      display:flex;
      flex-wrap:nowrap;
      justify-content:space-around;
    }
    .chosenRed{
      color:red;
      position:relative;
      padding-bottom:5px;
      border-bottom:2px solid red;
    }
    .cursordefault{
        cursor:default;
    }
    .cursordefault:after{
        content:"";
        height:100%;
    }
    .red{
        color:red;
    }
    .gray{
        color:gray
    }
    .gray:hover{
        color:red
    }
    .selectable:hover{
      color:red;
    }
    .more{
      color:red;
      float:right;
      display:inline-block;
    }
    .more:hover{
      cursor:pointer;
    }
    div#left{
      float:left;
      position:relative;
      top:10px;
      width:710px;
    }
    div#right{
      float:right;
      position:relative;
      top:10px;
      width:480px;
    }
    div#enterpriseHot,div#enterpriseMine,div#enterpriseOthers,div#advertisePlace{
        width:auto;
        padding:5px;
        border:2px solid rgb(222,222,222);
        margin-bottom:10px;
    }
    div#enterpriseHot:after{
        content:"企业热点";
        position:absolute;
        top:5px;
        left:1.5rem;
    }
    div#enterpriseHot:before{
        content:"";
        width:100%;
        position:absolute;
        top:2rem;
        left:0;
        border-top:2px solid rgba(222,222,222,1);
    }
    div#enterpriseHot>div.more{
        margin-bottom:10px;
    }
    div#hotTitle{
        position:relative;
        top:1.9rem;
        margin-bottom:2.9rem;
    }
    div#userCenter,div#takeNews,div#myDeal,div#recentActive,div#communityActive,div#other{
        min-height:200px;
        margin-top:5px;
    }
    div#advertisePlace{
        min-height:300px;
    }
    button{
        outline:none;
    }
    div#location,div#enterpriseDisplay,div#memberState{
        padding:5px;
        width:700px;
        border:2px solid rgba(222,222,222,1);
        margin-bottom:10px;
        position:relative;
    }
    div#enterpriseNews{
        display:inline-block;
        padding:5px;
        width:700px;
        height:420px;
        border:2px solid rgba(222,222,222,1);
        position:relative;
        overflow:hidden;
        margin-bottom:5px;
    }
    div#enterpriseNews:after{
        content:"企业新闻";
        position:absolute;
        left:1.7rem;
        top:5px;
        height:23px;
        line-height:23px;
    }
    div#memberState:after{
        content:"会员动态";
        position:absolute;
        left:1.7rem;
        height:23px;
        line-height:23px;
    }
    div#memberState{
        min-height:300px;
    }
    div#enterpriseNewsPics{
        width:700px;
        height:395px;
        margin-top:5px;
        overflow:hidden;
        white-space:nowrap;
        position:absolute;
    }
    div#picsSelect{
        position:absolute;
        bottom:35px;
        left:50%;
        z-index:999;
        transform:translateX(-50%);
    }
    button.selectButton{
        background:white;
        color:black;
        margin:0px 8px;
        border:0;
        width:1.2rem;
        height:1.2rem;
        font-size:0.8rem;
        border-radius:5px;
    }
    div.newsPicFrame{
        margin:0;
        width:700px;
        height:395px;
        display:inline-block;
        position:absolute;
        top:0;
        left:0;
    }
    img.newsPic{
        vertical-align:bottom;
        width:700px;
        height:395px;
    }
    .newsText{
        color:white;
        position:absolute;
        font-size:1rem;
        height:1.8rem;
        line-height:1.8rem;
        bottom:0px;
        left:0px;
        width:695px;
        padding-left:5px;
        background:rgba(0,0,0,0.3);
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
    }
    img.displayPic{
      width:100px;
      height:100px;
      margin:15px;
    }
    button:hover{
      cursor:pointer;
    }
    div#enterprise{
      width:1200px;
      margin:0 auto;
    }
    img.displayPic{
      width:100px;
      height:100px;
      margin:15px;
    }
    .more{
      color:red;
      float:right;
      display:inline-block;
    }
    .more:hover{
      cursor:pointer;
    }
    div#location{
      padding:5px;
      width:700px;
      border:2px solid rgba(222,222,222,1);
      margin-bottom:10px;
    }
    div#location,div.bigTitle{
      padding:5px;
      width:700px;
      border:2px solid rgba(222,222,222,1);
      margin-bottom:10px;
    }
    div.bigTitle{
      padding:15px 5px;
      text-align:center;
      font-size:1.8rem;
      font-weight:bold;
      letter-spacing:6px;
      margin:0;
    }
    div.famousFrame,div.membersFrame{
      text-align:center;
      margin-bottom:10px;
    }
    img.famousPic{
      height:150px;
      width:150px;
      margin:20px;
    }
    img.membersPic{
      height:120px;
      width:120px;
      margin:15px;
    }
    div#enterpriseHot,div#enterpriseMine,div#enterpriseOthers,div#advertisePlace{
      width:auto;
      padding:5px;
      border:2px solid rgb(222,222,222);
      margin-bottom:10px;
    }
    div#enterpriseHot:after{
      content:"企业热点";
      position:absolute;
      top:5px;
      left:1.5rem;
    }
    div#enterpriseHot:before{
      content:"";
      width:100%;
      position:absolute;
      top:2rem;
      left:0;
      border-top:2px solid rgba(222,222,222,1);
    }
    div#enterpriseHot>div.more{
      margin-bottom:10px;
    }
    div#hotTitle{
      position:relative;
      top:1.9rem;
      margin-bottom:2.9rem;
    }
    div#userCenter,div#takeNews,div#myDeal,div#recentActive,div#communityActive,div#other{
      min-height:200px;
      margin-top:5px;
    }
    div#advertisePlace{
      min-height:300px;
    }
}
</style>



