<template>
<div id="enterpriseRight">
  <div id="right">
      <div id="enterpriseHot">
        <div class="more">更多</div>
        <div id="hotTitle">
          <router-link to="/enterprise" v-for="i in enterpriseHot" class="gray" :key="i"><br>{{i}}<br></router-link>
        </div>
      </div>
      <div id="enterpriseMine">
        <div id="mineHeader">
          <div class="flexbox2">
            <span class="cursordefault" v-bind:class="{chosenRed:(mine==1)}" @mouseover="changeMine(1,$event)"><i class="fa fa-user" aria-hidden="true"></i> 用户中心 </span>
            <span class="cursordefault" v-bind:class="{chosenRed:(mine==2)}" @mouseover="changeMine(2,$event)"><i class="fa fa-file-text" aria-hidden="true"></i> 订阅资讯 </span>
            <span class="cursordefault" v-bind:class="{chosenRed:(mine==3)}" @mouseover="changeMine(3,$event)"><i class="fa fa-check-square-o" aria-hidden="true"></i> 我的交易 </span>
          </div>
          <div v-if="mine==1" id="userCenter">userCenter</div>
          <div v-if="mine==2" id="takeNews">takeNews</div>
          <div v-if="mine==3" id="myDeal">myDeal</div>
        </div>
      </div>
      <div id="enterpriseOthers">
        <div id="otherHeader">
          <div class="flexbox2">
            <span class="cursordefault" v-bind:class="{chosenRed:(others==1)}" @mouseover="changeOthers(1,$event)"><i class="fa fa-user" aria-hidden="true"></i> 近期活动 </span>
            <span class="cursordefault" v-bind:class="{chosenRed:(others==2)}" @mouseover="changeOthers(2,$event)"><i class="fa fa-file-text" aria-hidden="true"></i> 协会活动 </span>
            <span class="cursordefault" v-bind:class="{chosenRed:(others==3)}" @mouseover="changeOthers(3,$event)"><i class="fa fa-check-square-o" aria-hidden="true"></i> 其他 </span>
          </div>
          <div v-if="others==1" id="recentActive">recentActive</div>
          <div v-if="others==2" id="communityActive">communityActive</div>
          <div v-if="others==3" id="other">other</div>
        </div>
      </div>
      <div id="advertisePlace">
        广告招商
      </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'enterpriseRight',
  data(){
    return {
      newsPics:["./static/PICS/timg5.jpg","./static/PICS/timg5.jpg"],
      enterpriseHot:["新松机器人科技天团亮相世界机器人大会",
        "2018年上边年广东工业机器人产量占全国1/5",
        "机器视觉公司扩博智能完成1100万美元融资",
        "2018世界机器人大会在北京举办"
        ],
      mine:1,
      others:1,
      rightShow:0,
    }
  },
  methods:{
    changeMine:function(n,event){
      this.mine=n;
      //event.target.style.color='red';
    },
    changeOthers:function(n,event){
      this.others=n;
    }
  }
}
</script>
<style>
</style>



