<template>
  <div id="x_Text">

    <!-- <m-header></m-header> -->

    <div v-html="this.content" v-if="this.content!=null"></div>
    <div  v-if="this.content==null">{{Text}}</div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      Text:
         "前端累了，这个文章内容东西好像没有"
    };
  },
  props:{
    content:{  
      required:true
    }
  }
};
</script>

<style lang="scss" scoped>
#x_text {
  width: 100%;
  text-indent: 2em;
  height: 100%;
}
</style>




